@import 'config.less';
#Menu {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px 0;
  button {
    color: @lightTxt;
    background: none;
    height: 40px;
    i {
      font-size: 2.5rem;
    }
    h6 {
      font-size: 0.8rem;
    }
  }
}